import { Navbar } from 'react-bootstrap';
import React, { Component } from "react"
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from './Home';
import { GoogleLogin } from 'react-google-login';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

class Login extends Component {
    constructor(props) {
        super(props); this.state = {
            name: ''
        }
        this.handleLoginClick = this.handleLoginClick.bind(this);
        this.handleLogoutClick = this.handleLogoutClick.bind(this);
        this.state = { isLoggedIn: false };
    }

    handleLoginClick() {
        this.setState({ isLoggedIn: true });
    }
    handleLogoutClick() {
        this.setState({ isLoggedIn: false });
    }



    render() {
        const isLoggedIn = this.state.isLoggedIn, name = this.state.name;

        let button;
        if (isLoggedIn) {
            button = <button onClick={this.handleLogoutClick} />;
        } else {
            button = <button onClick={this.handleLoginClick} />;
        }

        const successLogin = (resp) => {
            console.log(resp.profileObj.name)
            this.setState({ isLoggedIn: true, name: resp.profileObj.name });
        }

        const failureLogin = (resp) => {
            this.setState({ isLoggedIn: false });
        }

        return (
            <div>
                {!isLoggedIn &&
                    <GoogleLogin
                        clientId="930160017922-47ok0thbf96dg1b0kkcib8hjeknnk6vh.apps.googleusercontent.com"
                        buttonText="Login"
                        onSuccess={successLogin}
                        onFailure={failureLogin}
                        cookiePolicy={'single_host_origin'}
                    />
                }
                {isLoggedIn &&

                    <Router>
                        <div>
                            <Navbar bg="dark" variant="dark">
                                <Navbar.Brand href="#home">Desafio - HOME</Navbar.Brand>
                            </Navbar>

                            <div className="container mt-3">
                                <Switch>
                                    <Route exact path={["/", "/home"]} component={Home} />
                                </Switch>
                            </div>
                        </div>
                    </Router>
                }
            </div>
        );
    }
};
export default Login;