import { Navbar, Alert, Form, Col, Button, Container, Row, Table } from 'react-bootstrap';
import React, { Component } from "react";
import loadFirebase from '../firebaseConfig';
import Login from './Login'
import 'bootstrap/dist/css/bootstrap.min.css';

const firebase = loadFirebase();

class Home extends Component {
    constructor(props) {
        super(props); this.state = {
            studentslist: [],
            names: '',
            lastNames: '',
            userId: '',
            nota1: 0,
            nota2: 0,
            nota3: 0,
            nota4: 0,
            nota5: 0
        }
        this.saveData = this.saveData.bind(this);
        this.saveDataNotes = this.saveDataNotes.bind(this);
        this.getStudent = this.getStudent.bind(this);
    }

    getStudents = async () => {
        const studentsList = [];
        const responseNotes = await firebase.database().ref('/Notes').once("value").then(snap => { return snap.val() });
        for (const key in responseNotes) {
            const lastData = responseNotes[key];
            const { nota1, nota2, nota3, userId, nota4, nota5, avg } = lastData;
            const { names, lastNames } = await firebase.database().ref('/Student').child(userId).once("value").then(s => { return s.val() });
            studentsList.push({ names, lastNames, nota1, nota2, nota3, nota4, nota5, avg });
        }
        this.setState({ studentslist: studentsList });
    }

    createStudent(data) {
        return firebase.database().ref('/Student').push(data).getKey();
    }

    createNotes(data) {
        return firebase.database().ref('/Notes').push(data);
    }

    update(key, value) {
        return firebase.database().ref('/Student').child(key).update(value);
    }

    delete(key) {
        return firebase.database().ref('/Student').child(key).remove();
    }

    saveData() {
        var names = this.state.names, lastNames = this.state.lastNames;

        let data = {
            names,
            lastNames
        };

        var id = this.createStudent(data);
        this.setState({ userId: id })
    }

    saveDataNotes() {

        var userId = this.state.userId;

        var nota1 = this.state.nota1, nota2 = this.state.nota2, nota3 = this.state.nota3, nota4 = this.state.nota4, nota5 = this.state.nota5;

        let values = [Number(nota1), Number(nota2), Number(nota3), Number(nota4), Number(nota5)];

        let sum = values.reduce((previous, current) => current += previous);
        let avg = sum / values.length;

        let data = {
            userId,
            nota1,
            nota2,
            nota3,
            nota4,
            nota5,
            avg
        };

        this.createNotes(data).then(() => {
            console.log("Created new item successfully!");
        })
            .catch((e) => {
                console.log(e);
            });;
    }

    getStudent() {
        return (
            <input type="button" name="address"
                onClick={this.onAddressChanged} />
        )
    }

    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    componentDidMount() {
        this.getStudents();
    }


    render() {

        console.log(this.state.studentslist)

        return (
            <div>
                <Alert variant="success">
                    <Alert.Heading>Hey, nice to see you {this.props.name}</Alert.Heading>
                    <hr />
                </Alert>
                <Container>
                    <Row>
                        <Col><Form>
                            <Form.Row>
                                <Form.Group as={Col} controlId="formGridZip">
                                    <Form.Label>Nombres</Form.Label>
                                    <Form.Control type="text" name="names" placeholder="Ej. Eduardo" onChange={(e) => { this.handleChange(e) }} />
                                </Form.Group>

                                <Form.Group as={Col} controlId="formGridPassword">
                                    <Form.Label>Apellidos</Form.Label>
                                    <Form.Control type="text" name="lastNames" placeholder="Ej. Lopez" onChange={(e) => { this.handleChange(e) }} />
                                </Form.Group>
                            </Form.Row>

                            <Button onClick={this.saveData}>Guardar</Button>
                        </Form></Col>
                    </Row>
                </Container>
                <br />
                <Container>
                    <Row>
                        <Col><Form>
                            <Form.Row>
                                <Form.Group controlId="formGridAddress1">
                                    <Form.Label>Nota 1</Form.Label>
                                    <Form.Control type="number" name="nota1" placeholder="Ej. 8" onChange={(e) => { this.handleChange(e) }} />
                                </Form.Group>
                                <Form.Group controlId="formGridAddress1">
                                    <Form.Label>Nota 2</Form.Label>
                                    <Form.Control type="number" name="nota2" placeholder="Ej. 8" onChange={(e) => { this.handleChange(e) }} />
                                </Form.Group>

                                <Form.Group controlId="formGridAddress1">
                                    <Form.Label>Nota 3</Form.Label>
                                    <Form.Control type="number" name="nota3" placeholder="Ej. 8" onChange={(e) => { this.handleChange(e) }} />
                                </Form.Group>

                                <Form.Group controlId="formGridAddress1">
                                    <Form.Label>Nota 4</Form.Label>
                                    <Form.Control type="number" name="nota4" placeholder="Ej. 8" onChange={(e) => { this.handleChange(e) }} />
                                </Form.Group>

                                <Form.Group controlId="formGridAddress1">
                                    <Form.Label>Nota 5</Form.Label>
                                    <Form.Control type="number" name="nota5" placeholder="Ej. 8" onChange={(e) => { this.handleChange(e) }} />
                                </Form.Group>
                            </Form.Row>
                            <Button onClick={this.saveDataNotes}>Guardar</Button>
                        </Form></Col>
                    </Row>
                </Container>
                <br />
                <Container>
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>First Name</th>
                                <th>Last Name</th>
                                <th>Nota 1</th>
                                <th>Nota 2</th>
                                <th>Nota 3</th>
                                <th>Nota 4</th>
                                <th>Nota 5</th>
                                <th>Promedio</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.studentslist.map(function (user, i) {
                                console.log(i)
                                return (<tr key={i}>
                                    <td>{i + 1}</td>
                                    <td>{user.names}</td>
                                    <td>{user.lastNames}</td>
                                    <td>{user.nota1}</td>
                                    <td>{user.nota2}</td>
                                    <td>{user.nota3}</td>
                                    <td>{user.nota4}</td>
                                    <td>{user.nota5}</td>
                                    <td>{user.avg}</td>
                                </tr>)
                            })}
                        </tbody>
                    </Table>
                </Container>

            </div>
        );
    }
};
export default Home;